<template>
	<view class="user">
		<view class="user-info">
			<view class="avatar">
				<image :src="accountInfo.url" mode="widthFix"></image>
			</view>
			<view class="username">
				{{accountInfo.username}}
			</view>
		</view>
		<view class="operate">
			<view class="item">
				<navigator url="../userRank/userRank" class="opt" hover-class="none">
					<image src="data:image/svg+xml;base64,"
					 mode="widthFix"></image>
					<text>会员等级</text>
				</navigator>
				<text class="icon icon-keyboard_arrow_right"></text>

			</view>
			<view class="item">
				<navigator url="../takeOrder/takeOrder" class="opt" hover-class="none">
					<image src="data:image/svg+xml;base64,"
					 mode="widthFix"></image>
					取餐
				</navigator>
				<text class="icon icon-keyboard_arrow_right"></text>
			</view>
			<view class="item">
				<navigator url="../orderList/orderList" class="opt" hover-class="none">
					<image src="data:image/svg+xml;base64,"
					 mode="widthFix"></image>
					订单中心
				</navigator>
				<text class="icon icon-keyboard_arrow_right"></text>
			</view>
			<view class="item">
				<view url="" class="opt">
					<image src="data:image/svg+xml;base64,"
					 mode="widthFix"></image>
					版本号
				</view>
				<text class="item-right">Version:0.0.1</text>
			</view>

		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				accountInfo: {
					url: '../../static/logo.png',
					username: '铅笔'
				}
			}
		},
		created() {
			// this.accountInfo = uni.getAccountInfoSync();
			console.log(uni.getAccountInfoSync())
		},
		methods: {

		}
	}
</script>

<style>
	.user {
		background-color: #F1F1F1;
		width: 100vw;
		height: 100vh;
	}

	.user-info {
		display: flex;
		height: 180rpx;
		background-color: #F8F8F8;
		align-items: center;
		margin-bottom: 50rpx;
		border-bottom: 2rpx solid rgba(7, 17, 27, 0.1);
	}

	.avatar {
		width: 128rpx;
		height: 128rpx;
		margin: 0 50rpx;
	}

	.avatar image {
		width: 100%;
		height: 100%;
		border-radius: 50%;
	}

	.username {
		font-size: 38rpx;
		font-weight: 700;
		line-height: 28rpx;
	}

	.operate {
		display: flex;
		flex-direction: column;
		align-items: center;
		background-color: #F8F8F8;
		border-radius: 20rpx;
		width: 94%;
		justify-content: space-between;
		margin: 0 auto;
		padding: 0 20rpx;
	}

	.item {
		display: flex;
		align-items: center;
		font-size: 36rpx;
		/* line-height: 72rpx; */
		height: 72rpx;
		/* margin: 20rpx 0; */
		padding: 52rpx 0;
		justify-content: space-between;
		width: 100%;
		position: relative;
	}

	.item::after {
		content: '';
		display: block;
		left: 0;
		bottom: 0;
		width: 100%;
		position: absolute;
		border-top: 2rpx solid rgba(7, 17, 27, 0.1);
	}

	.item:last-child::after {
		border: none;
	}

	.item image {
		width: 52rpx;
		height: 52rpx;
		margin-right: 20rpx;
	}

	.opt {
		display: flex;
		align-items: center;
		width: 100%;
	}
</style>
